// MORE TEXT BTN
.link-main {
   display: block;
   padding: 5px 0;
   //color: green
   font-size: 16px; }
.more-btn {
   display: inline-block;
   vertical-align: top;
   font-size: 14px;
   line-height: 20px;
   background-color: transparent;
   outline: none;
   color: #265992;
   @include segoe-r;
   &::after {
      margin-left: 3px;
      content: '';
      display: inline-block;
      vertical-align: middle;
      @include s('arrow_d'); }
   &.is-active::after {
      @include rotate(180); }
   &--blue {
      @include r(767) {
         display: block;
         margin-top: 5px;
         font-size: 14px;
         line-height: 1.5em; } }
   &--adapt {
      display: none;
      @include r(767) {
         margin: 10px auto; } }

   &--partners {
      margin-top: 10px;
      @include r(767) {
         display: none; } }
   &--testimonials {
      line-height: 20px;
      display: none;
      @include r(767) {
         display: inline-block;
         //font-size: 24px
 } }         //line-height: 31px
   &--testimonials::after {
      @include r(767) {
         //+s('arrow_d_lg')
         margin-left: 5px; } }
   &--feed {
      font-size: 13px;
      line-height: 24px;
      color: #19b24c;
      @include r(767) {
         //font-size: 20px
 }         //line-height: 37px
      &::after {
         content: '';
         @include s('arr_d-gray'); } } }

