.click-title {
   display: inline-block;
   position: relative;
   @include pfCond-m;
   font-size: 20px;
   text-transform: uppercase;
   z-index: 1;
   color: #333333;
   .col-1-4 & {
      @include r(1200) {
         font-size: 16px; } }
   @include r(990) {
      font-size: 17px; }
   &::before {
      content: '';
      position: absolute;
      right: 10px;
      top: 17px;
      z-index: 1;
      @include s('clicker');
      pointer-events: none; }
   &::after {
      position: absolute;
      bottom: 2px;
      right: 0;
      width: 55px;
      content: '';
      height: 2px;
      background-color: #333333; } }
