.akp {
   &__list {
      padding: 35px 0;
      border-bottom: solid 2px #f8cb39;
      @include r(1024) {
         @include clr; } }
   &__item {
      display: inline-block;
      //width: 140px
      text-align: center;
      vertical-align: top;
      margin-left: 65px;
      &:first-child {
         margin-left: 0;
         @include r(767) {
            margin: 0 auto; }
         @include r(600) {
 } }            //margin-left: 0
      @include r(1024) {
         float: left;
         //display: inline-block
         margin-left: calc((100% - 700px)/4); }
      @include r(767) {
         display: block;
         float: none;
         margin: 25px auto;
         position: relative;
         width: auto; }

      a {
         width: 140px;
         display: block;
         @include r(767) {
            display: inline-block;
            @include clr;
            width: auto; } }

      a:hover {
         .akp__image {
            background: rgb(255,255,255);
            background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,245,210,1) 29%,rgba(255,245,210,1) 99%,rgba(255,245,210,1) 100%,rgba(255,245,210,1) 100%,rgba(255,245,210,1) 100%,rgba(255,245,210,1) 100%,rgba(32,124,202,1) 100%,rgba(255,245,210,1) 100%); }
         .akp__descr {
            color: #ffc633; } }
      img {
         display: inline-block;
         vertical-align: middle; } }
   &__image {
      width: 100%;
      height: 140px;
      padding: 18px 0 0 0;
      border: solid 2px #f8cb39;
      border-radius: 50%;
      @include r(767) {
         float: left;
         max-width: 140px;
         width: 140px; } }

   &__name {
      color: #19b14c;
      @include pfCond-b;
      font-size: 18px;
      padding-top: 5px;
      text-align: center; }
   &__descr {
      font-size: 13px;
      padding-top: 15px;
      @include r(767) {
        //  position: absolute
        //  top: 0
        //  //left: 0
        //  bottom: 0
        //  margin: auto
        //  left: 150px
        //  text-align: left
        font-size: 18px;
        float: left;
        max-width: 140px;
        text-align: left;
        margin-left: 15px; }
      @include r(600) {
         margin: 0 0 0 15px;
         max-width: 140px;
         position: relative;
         float: left;
         width: 140px;
         top: auto;
         left: auto;
         bottom: auto;
         right: auto; }
      @include r(480) {
         font-size: 14px;
         padding: 35px 0 0 0;
         margin: 0 0 0 5px;
         width: 110px; } } }
