.silver {
   background-color: #f3f3f3;
   position: relative;
   padding: 40px 0 65px;
   margin-bottom: 35px;
   &__title {
      color: #555;
      font-family: "Myriad Pro";
      font-size: 26px;
      line-height: 2em;
