.grid-row {
   @include clr;
   margin: 0 -22px;

   @include r(1200) {
      margin: 0 -16px; }
   @include r(990) {
      margin: 0 -6px; }
   @include r(767) {
      margin: 0; }
   @for $i from 1 through 12 {
      .col-#{$i} {
          width: calc( 8.33333% * #{$i} );
          padding: 0 22px;
          float: left;
          @include r(1200) {
            padding: 0 16px; }
          @include r(990) {
             padding: 0 6px; }
          @include r(767) {
             padding: 0;
             float: none;
             width: 100%;
             max-width: 480px;
             margin: 15px auto; } } } }



