.slider-vertical {
   @include r(767) {
      margin-bottom: 50px; }
   &__box {
      padding: 60px 0;
      position: relative;
      text-align: center;
      width: 185px;
      @include r(767) {
         width: 100%;
         padding: 0 10px; }
      .arrow-top, .arrow-bottom {
         position: absolute;
         left: 0;
         right: 0;
         margin: auto;
         width: 62px;
         @include r(767) {
            transform: rotate(90deg);
            top: 0;
            bottom: 0;
            left: auto;
            right: auto;
            z-index: 10; } }

      .arrow-top {
         top: 25px;
         @include r(767) {
            right: -30px; }
         @include r(480) {
            right: 0; } }
      .arrow-bottom {
         bottom: 25px;
         @include r(767) {
            left: -30px; }
         @include r(480) {
            left: 0; } } }
   &__inner {
      margin-bottom: -30px;
      @include r(767) {
         margin: 0 auto; } }
   &__slide {
      display: block;
      margin-top: 30px;


      &:first-of-type {
         margin-top: 0; }
      &:last-of-type {
         margin-top: 0; }
      img {
         width: 185px; }
      @include r(767) {
         margin-top: 0; } } }
