@include font(PFDinTextCompPro-Regular, PFDinTextCompPro-Regular);
@include font(PFDinTextCompPro-Medium, PFDinTextCompPro-Medium);
@include font(PFDinTextCondPro-Regular, PFDinTextCondPro-Regular);
@include font(PFDinTextCondPro-Medium, PFDinTextCondPro-Medium);
@include font(PFDinTextCondPro-Bold, PFDinTextCondPro-Medium);
@include font(SegoeUI-Regular, SegoeUI-Regular);
@include font(SegoeUI-Italic, SegoeUI-Italic);
@include font(SegoeUI-Bold, SegoeUI-Bold);
@include font(SegoeUI-SemiBold, SegoeUI-SemiBold);

$default-fonts: Arial, sans-serif;

@mixin pfComp-r {
   font-family: PFDinTextCompPro-Regular, $default-fonts; }
@mixin pfComp-m {
   font-family: PFDinTextCompPro-Medium, $default-fonts; }

@mixin pfCond-r {
   font-family: PFDinTextCondPro-Regular, $default-fonts; }
@mixin pfCond-m {
   font-family: PFDinTextCondPro-Medium, $default-fonts; }
@mixin pfCond-b {
   font-family: PFDinTextCondPro-Bold, $default-fonts; }

@mixin segoe-r {
   font-family: SegoeUI-Regular, $default-fonts; }
@mixin segoe-i {
   font-family: SegoeUI-Italic, $default-fonts; }
@mixin segoe-b {
   font-family: SegoeUI-Bold, $default-fonts; }
@mixin segoe-sb {
   font-family: SegoeUI-SemiBold, $default-fonts; }
