.address {
   padding: 15px 0;
   &__title {
      margin-bottom: 50px;
      @include r(767) {
         margin-bottom: 25px; } }
   &__title-mark {
      display: inline-block;
      vertical-align: inherit;
      margin-right: 10px;
      font-size: 24px; }
   &__street {
      padding: 40px 0 30px;
      border-top: 2px dashed #ffc633;
      max-width: 990px;
      margin: 0 auto;
      @include r(767) {
         padding: 15px 0 30px; }
      &-title {
         max-width: 990px;
         margin: 0 auto;
         padding: 35px 15px 20px;
         font-size: 32px;
         line-height: 1em;
         color: #333333;
         text-align: center;
         @include pfCond-b;
         //+r(767)
         //   padding: 25px 0 20px
         //   font-size: 40px
         @include r(480) {
            font-size: 24px; }
         span {
            @include pfCond-r;
            @include r(510) {
               display: block; } } }
      &-inner {
         max-width: 960px;
         margin: 15px auto 0; } }
   &__street-label, &__video-label {
      margin-bottom: 10px;
      margin-left: 10px;
      font-size: 24px;
      line-height: 32px;
      color: #646466;
      @include pfCond-r;
      @include r(767) {
         text-align: center;
         //font-size: 36px
 }         //margin-bottom: 20px
      @include r(480) {
         font-size: 18px; } }

   &__map {
      position: relative; }
   &__map-holder {
      @include r(767) {
         height: 400px; }
      iframe {
         width: 100%;
         @include r(767) {
            height: 100%; } } }
   &__map-label {
      padding: 10px 35px 15px;
      position: absolute;
      right: 60px;
      top: 0;
      z-index: 1;
      background-color: #ffffd8;
      text-align: center;
      font-size: 18px;
      @include r(1023) {
         right: 40px;
         padding: 10px 20px 15px; }
      @include r(767) {
         right: 0; }
      @include r(730) {
         position: relative;
 }         //font-size: 22px
      @include r(480) {
         font-size: 12px; } }
   &__map-subtitle {
      margin: 10px 0 5px;
      color: #ffc633;
      @include segoe-sb; }
   &__map-subtitle + &__map-subtitle {
      margin-top: 0;
      @include segoe-b; }
   &__map-work {
      color: #000;
      @include segoe-b; }
   &__video-wrapper {
      max-width: 960px;
      margin: 20px auto 0;
      @include r(767) {
         margin: 50px auto 0; } }
   &__video-title {
      margin-bottom: 15px;
      font-size: 18px;
      @include pfCond-m;
      @include r(767) {
         //font-size: 28px
 }         //text-align: left
      span {
         font-size: 15px;
         @include r(767) {
            font-size: 22px; } } }
   &__video-inner {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      @include r(1023) {
         justify-content: space-around;
         align-items: flex-end; }
      @include r(767) {
         padding: 0 5px; } }
   &__video {
      flex-basis: 33.33333%;
      text-align: center;
      margin-bottom: 20px;
      @include r(767) {
         flex-basis: 100%;
         margin-bottom: 60px;
         height: 480px;
         text-align: center; }
      @include r(480) {
         height: auto; }
      iframe {
         @include r(767) {
            height: 100%;
            width: 100%; }
         @include r(480) {
            height: 220px; } } }
   &__video-label {
      margin: 15px 0 25px 10px; }

   &--oil {
      padding-top: 100px;
      .address__street {
         padding: 0 0 40px; }
      .address__street-title {
         text-align: left;
         @include r(767) {
            text-align: center; } } }

   &--oil-page {
      padding: 0;
      .address__street {
         margin: 90px 0 100px;
         padding: 50px 0 45px;
         box-shadow: 0 4px 15px rgba(1, 1, 1, 0.08);
         background-color: #fff;
         border: none;
         @include r(767) {
            margin: 0;
            padding-top: 15px; } }
      .address__street-title {
         padding: 0 15px 0; }
      .address__street-inner {
         margin: 20px 0 0; }
      .address__video-inner {
         max-width: 640px;
         margin: 0 auto; }
      .address__video {
         margin: 0 10px 25px;
         @include r(768) {
            margin-bottom: 55px; } }
      .address__map-label {
         padding: 10px 25px 15px;
         right: 40px;
         @include r(767) {
            right: 0;
            padding: 5px 10px 5px; } }
      .address-map__phone {
         @include r(767) {
            font-size: 14px; } }
      .address__map-subtitle {
         @include r(767) {
            font-size: 16px; } }
      .address__map-work {
         @include r(767) {
            font-size: 14px; } }

      .address__street-label, .address__video-label {
         margin-left: 20px; } } }

// ADRESS LINKS
.address-links {
   padding: 25px 0 30px;
   border-top: 2px dashed #ffc633;
   border-bottom: 2px dashed #ffc633;
   &__inner {
      max-width: 820px;
      margin: 0 auto;
      font-size: 0; }
   &__row {
      margin: 0 -10px;
      @include clr; }
   &__head-title {
      margin-bottom: 25px;
      font-size: 30px;
      line-height: 37px;
      text-align: center;
      color: #333333;
      @include pfCond-m;
      @include r(990) {
         font-size: 28px; } }
   &__item {
      padding: 0 10px;
      float: left;
      width: 50%;
      text-align: center;
      &:nth-child(2) {
         float: right; }
      @include r(380) {
         float: none;
         width: 100%;
         display: block;
         padding-bottom: 20px; } }
   &__title {
      font-size: 36px;
      line-height: 36px;
      @include pfCond-b;
      font-weight: 700;
      @include r(990) {
         font-size: 32px; }
      @include r(767) {
         font-size: 24px;
         line-height: 1.5em; } }
   &__address {
      max-width: 270px;
      margin: 15px auto 25px;
      font-size: 20px;
      line-height: 27px;
      @include r(990) {
         font-size: 18px; }
      span {
         @include segoe-b; }
      @include r(767) {
         max-width: 190px;
         font-size:  14px;
         line-height: 1.5em;
         min-height: 42px; } }
   &__btn {
      display: inline-block;
      padding: 0 48px;
      height: 35px;
      line-height: 35px;
      font-size: 18px;
      background-color: #ffcd00;
      letter-spacing: 0.01em;
      @include trs;
      @include pfComp-r;
      font-weight: 700;
      @include r(767) {
         height: 45px;
         line-height: 45px;
         span {
            display: none; } }
      @include r(480) {
         height: 35px;
         line-height: 35px;
         padding: 0 15px; }
      &:hover {
         background-color: #ffae00; } }
   &--autoserv {
      margin: 55px 0 65px;
      @include r(1023) {
         margin: 25px 0; } }

   &--oil-page {
      padding: 35px 0 50px;
      margin-top: 30px;
      box-shadow: 0 4px 15px rgba(1, 1, 1, 0.08);
      background-color: #fff;
      border-top: 2px dashed #ffc633;
      border-bottom: 2px dashed #ffc633;
      @include r(768) {
 }         //display: none
      .address-links__btn {
         padding: 0 32px;
         @include r(990) {
            padding: 0 10px;
            font-size: 14px; } }
      @include r(480) {
         .address-links__item {
            display: block;
            float: none;
            margin: 15px auto;
            width: 80%; } }
      .address-links__inner {
         max-width: 720px; } } }
