.oil-nav {
   position: fixed;
   left: 50%;
   margin-left: -600px;
   z-index: 10;
   font-size: 0;
   @include r(1229) {
      margin-left: -497px; }
   @include r(1023) {
      margin-left: -376px; }
   @include r(767) {
      display: none; }
   &__item {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      background-color: #fff;
      border-bottom: 1px solid #bfbfbf;
      border-top: none;
      font-size: 12px;
      &:first-child {
         border-left: 1px solid #bfbfbf;
         border-bottom-left-radius: 10px; }
      &:last-child {
         border-right: 1px solid #bfbfbf;
