.about-us {
   padding: 45px 0 15px;
   &__title {
      margin-bottom: 25px;
      font-size: 38px;
      text-align: center; }
   &__inner {
      max-width: 1000px;
      margin-top: 60px;
      @include clr;
      @include r(1023) {
         margin-top: 20px; }
      @include r(767) {
         margin-top: 30px; } }
   &__stamp {
      float: left;
      margin-top: -20px;
      margin-right: 50px;
      @include r(1023) {
         margin-right: 20px;
         margin-top: 0; }
      @include r(767) {
         display: inline-block;
         vertical-align: middle;
         float: none;
         margin: 0; }
      @include r(480) {
         text-align: center;
         display: block; } }

   &__remark {
      @include r(767) {
         display: inline-block;
         vertical-align: middle;
         width: 65%;
         margin-left: 6%; }
      @include r(480) {
         width: 100%;
         margin-left: 0; }
      &-text {
         line-height: 20px;
         @include r(767) {
            font-size: 14px;
            line-height: 1.5em; } }
      &-bold {
         @include segoe-b; }
      &-hidden {
         display: none; } }
   &__nav {
      text-align: left;
      font-size: 0;
      font-weight: normal;
      &-link {
         display: inline-block;
         text-decoration: underline;
         font-size: 13px;
         line-height: 20px;
         @include tr(color .1s);
         @include r(767) {
            font-size: 14px;
            line-height: 1.5em; }

         &::after {
            display: inline-block;
            content: '/';
            margin-left: 5px;
            margin-right: 5px; }

         &:last-child {
            margin-right: 0; }
         &:last-child::after {
            content: none; }
         &:hover {
            color: #ff9600; }
         &:hover::after {
            color: #000; } } } }
.stamp {
   text-align: center;
   @include r(480) {
      text-align: center;
      margin: 0 0 15px 0; }
   &__icon {
      position: relative;
      display: inline-block;
      width: 121px;
      height: 121px;
      vertical-align: top;
      &:before {
         content: '';
         position: absolute;
         @include s(stamp);
         left: 0; } }


   &__label {
      margin-top: 10px;
      @include segoe-sb;
      font-size: 14px; } }

.reasons {
   padding: 15px 0 40px;
   @include r(1023) {
      padding-bottom: 25px; }
   @include r(767) {
      padding-bottom: 20px; }
   @include r(480) {
      padding-bottom: 10px; }
   &__title {
      text-align: center;
      margin-bottom: 50px;
      @include r(1023) {
         margin-bottom: 30px; }
      @include r(767) {
         margin-bottom: 15px; } } }

.autoserv {
   padding: 15px 0 40px;
   @include r(1023) {
      padding-bottom: 30px; } }

.serv-slider {
   padding: 0 20px;
   @include r(767) {
      margin: 15px 0px; }
   &__item {
      margin: 0 15px;
      //padding: 2px
      display: block;
      //border: 1px solid #dadada
      //border-radius: 3px
      text-align: center; }
   &__img {
      //width: 100%
      //height: 100%
      padding: 2px;
      //display: block
      border: 1px solid #dadada;
      border-radius: 3px; }
   .slick-arrow {
      position: absolute;
      top: calc(50% - 10px);
      z-index: 1;
      font-size: 0;
      background-color: transparent;
      @include r(767) {
         top: calc(50% - 38px);
         border: none; }
      @include r(480) {
         transform: scale(0.6); } }
   .slick-prev {
      left: 0;
      @include arr(10, 20, #888888, l);
      @include r(767) {
         @include s('arrow-sld');
         left: -5px; } }
   .slick-next {
      right: 0;
      @include arr(10, 20, #888888, r);
      @include r(767) {
         @include s('arrow-sld');
         transform: rotate(180deg);
         right: -5px; }
      @include r(480) {
         transform: rotate(180deg) scale(0.6); } }
   .slick-disabled {
      opacity: .7;
      @include r(767) {
         opacity: .4; } } }

