.price-accordion {
   font-size: 13px;
   @include r(767) {
 }      //font-size: 22px
   &__title {
      padding: 7px 17px;
      color: #fff;
      background-color: #000101;
      @include clr;
      @include r(767) {
 }         //padding: 16px 25px
      &-text {
         float: left; }
      &-price {
         float: right; } }
   &__item {
      color: #434343;
      &.is-active {
         pointer-events: none;
         @include r(767) {
            pointer-events: auto; }
