         cursor: pointer;
         @include tr(background-color .25s);
         @include r(767) {
 } }            //padding: 8px 50px 8px 25px
      &-inner {
         border-top: 1px solid #fff;
         border-bottom: 1px solid #fff;
         padding: 4px 17px;
         background-color: #eeeeee;
         @include clr;
         @include r(767) {
            //padding: 8px 25px
            padding: 4px 6px; } }
      &-content {
         display: none; }
      &-text {
         float: left;
         @include r(480) {
            max-width: 80%; } }
      &-price {
         float: right; } }
   &__icon {
      position: absolute;
      z-index: 1;
      right: 15px;
      font-size: 15px;
      top: 0;
      bottom: 0;
      margin: auto;
